@include font(poppins, poppins);
@include font(poppins-semibold, poppins-semibold);

@mixin poppins {
	font-family: 'poppins', sans-serif; }

@mixin poppins-semibold {
	font-family: 'poppins-semibold', sans-serif; }

body {
  @include poppins;
  line-height: 1.2; }

.l-center {
  max-width: 1310px;
  margin: 0 auto;
  padding: 0 15px; }

.button {
  display: inline-block;
  padding: 12px 24px;
  text-decoration: none;
  @include poppins-semibold;
  font-size: 24px;
  background: #d0554c;
  color: #fff;
  box-shadow: 3px 5.196px 13px 0px rgba(0, 0, 0, 0.5);
  line-height: 1.1;
  @include r(1440) {
    font-size: 20px; }
  @include r(767) {
    font-size: 18px; }
  &:hover {
    background: saturate(#d0554c, 10%); } }


.heading {
  padding: 63px 0;
  text-align: center;
  @include poppins-semibold;
  color: #353846;
  @include r(1200) {
    padding: 40px 0; }
  @include r(767) {
    padding: 30px 0;
    br {
      display: none; } }
  &--bg-white {
    @include r(1200) {
      background: #fff; } }
  &--more-padding {
    padding: 94px 0;
    @include r(1200) {
      padding: 40px 0; }
    @include r(767) {
      padding: 30px 0; } }
  &__title {
    font-size: 48px;
    @include r(1440) {
      font-size: 35px; }
    @include r(1200) {
      font-size: 30px; }
    @include r(767) {
      font-size: 25px; } } }
